@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.card {
  position: relative;
  -webkit-animation: flip 500ms ease-in;
          animation: flip 500ms ease-in;
}

.card img {
  width: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

.card .front {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  position: absolute;
}

.flipped .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #1b1523;
}
.modal {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding: 30px;
  max-width: 480px;
  margin: auto;
  border-radius: 10px;
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1em;
  text-align: center;
  background: #1b1523;
  color: #fff;
  margin: auto;
  -webkit-animation: softer 1s ease-in;
          animation: softer 1s ease-in;
}

@-webkit-keyframes softer {
0% {
  opacity: 0%;
}
100% {
  opacity: 100%; 
}
}

@keyframes softer {
0% {
  opacity: 0%;
}
100% {
  opacity: 100%; 
}
}

.play-again {
    background: none;
    border: 2px solid #fff;
    padding: 6px 12px;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
  }

  .play-again:hover {
    background: #c23866;
    color: #fff;
  }

  
  



.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #cd2aa7fa;
  color: #fff;
}

.card-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
  -webkit-animation: flip 1s ease-in;
          animation: flip 1s ease-in;
}

@-webkit-keyframes flip {
0% {
  opacity: 0%;
}
100% {
  opacity: 100%; 
}
}

@keyframes flip {
0% {
  opacity: 0%;
}
100% {
  opacity: 100%; 
}
}
